<template>
    <div class="loading" v-if="loading">
      <Spin size="large"></Spin>
    </div>
</template>

<script>
export default {
  name: 'loading',
  data () {
    return {
      loading: this.$store.getters.loading
    }
  }
}
</script>

<style lang="scss" scoped>
  .loading {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    background-color: rgba(255,255,255,0.8);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 9999;
  }
</style>


